
body{
    margin: 0;
    background-color: whitesmoke;
}

p {
    text-align: left;
    overflow: auto;
    padding-right: 2%;
    padding-left:2%;
}

a {
    text-decoration: none;
    font-weight: bold;
}

a:hover{
    text-decoration: none;
}

/*
    bootstrap
*/

.text-right{
    font-size: 0.8em;
}

.card{
    margin: 1%;
    border-radius: 0;
    border-color: rgb(0, 0, 0);
    min-width: auto;
    overflow: auto;
}

.card-img-top{
    margin-top: 20px;
    width: 80%;
    border-radius: 0;
    border-color: rgb(0, 0, 0);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}


/*
    p√°gina
*/

.location{
    font-size: 0.8em;
}

.obs{
    font-size: 0.8em;
}

.alinha{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.alinha small, a{
    margin: 0 5px 0 5px;
}

.line{
    width: 80%;
}

.foot{
    color: black;
    text-align: center;
    font-family: monospace;
    padding:10px;
    font-weight: bold;
    right: 0;
    bottom: 0;
    left: 0;
}

.mainpage {
    font-family: 'Courier New', Courier, monospace;
    margin: 0 5% 0 5%;
    text-align: center;
    font-size: 1.2em;
}

#myname{
    text-align: center;
    margin-top: 5%;
    font-size:2em;
    font-weight: bold;
    color: #2b2b2b;
}

.topic{
    padding: 2%;
}

/*
    span
*/

.letter{
    font-size: 1.5em;
    font-weight: bold;
    padding: 10px;
    background-color: black;
    color: white;
}

.focus{
    background-color: yellow;
    color: black;
}

/*
    logos
*/

.logosgroup {
    display: block;
    text-align: center;
  }

.logos {
    cursor:pointer;
    padding:2%;
    font-size: 2em;
    color: #5a5a5a;
}

.logos:hover{
    color: rgb(16, 16, 95);
}

.footname{
    font-size: 0.8em;
}
